@-webkit-keyframes moving_right_to_left {
  from {
    opacity: 0;
    -webkit-transform: translateX(50%);
            transform: translateX(50%);
  }
  20% {
    opacity: 1;
    -webkit-transform: translateX(30%);
            transform: translateX(30%);
  }
  80% {
    opacity: 1;
    -webkit-transform: translateX(-36%);
            transform: translateX(-36%);
  }
  to {
    opacity: 0;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}

@keyframes moving_right_to_left {
  from {
    opacity: 0;
    -webkit-transform: translateX(50%);
            transform: translateX(50%);
  }
  20% {
    opacity: 1;
    -webkit-transform: translateX(30%);
            transform: translateX(30%);
  }
  80% {
    opacity: 1;
    -webkit-transform: translateX(-36%);
            transform: translateX(-36%);
  }
  to {
    opacity: 0;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}

.moving-right-to-left {
  position: relative;
  top: -10%;
  -webkit-transform-origin: 50% 50% 0;
      -ms-transform-origin: 50% 50% 0;
          transform-origin: 50% 50% 0;
  -webkit-animation: 2s linear 0s infinite normal both running moving_right_to_left;
          animation: 2s linear 0s infinite normal both running moving_right_to_left;
}

.static-bottom {
  position: relative;
  top: -1%;
}


